.about {
    animation: one 2000ms 1 ease-in;
    position: absolute;
    top:5%;
    left:2%;
    background-color: #124861b0;
    color:#61dafb;
    border: solid 1px #61dafb;
    padding:0 0.25em;
    h2 {
        text-transform: uppercase;
        font-weight: 100;
        
        font-size:35px;
    }
    .wrapper {
        padding: 0.5em;
        display: flex;
        .one {
            content: '';
            font-size:10px;
        }
        .two {
        
            ul {
                font-size:15px;
                list-style-type: none;
                text-align: left;
                li {
                    display: flex;
                    justify-content: space-between;
                    ul {
                        li {
                            display:flex;
                            justify-content: flex-end;
                        }
                    }
                    
                }
            }
        }
    }
}
@keyframes one {
    0%{
      transform: scale(0%);
    }
    10% {
      transform: scaleX(10%);
      transform: scaleY(5%);
    }
    15% {
      transform: scaleX(20%);
      transform: scaleY(10%);
    }
    20% {
      transform: scaleX(40%);
      transform: scaleY(15%);
    }
    25% {
      transform: scaleX(60%);
      transform: scaleY(35%);
    }
    30% {
      transform: scaleX(80%);
      transform: scaleY(50%);
    }
    35% {
      transform: scaleY(40%);
      transform: scaleX(100%);
    }
    70% {
      transform: scaleY(100%);
    }
  }



@media (max-width:768px) {
  .about {
    width:100vw;
    height:300px;
    top:0;
    left:0;
    padding:0 0;
    h2 {
      padding:0;
    }
    .wrapper {
      display: flex;
      padding:0;
      .one {
          content: '';
          font-size:5px;
      }
      .two {
          ul {
              font-size:13px;
              list-style-type: none;
              text-align: left;
              li {
                  display: flex;
                  justify-content: space-between;
                  ul {
                      li {
                          display:flex;
                          justify-content: flex-end;
                      }
                  }
                  
              }
          }
      }
  }
  }
}