.Social-Wrapper {
    animation: one 2600ms 1 ease-in;
    position: absolute;
    bottom:5%;
    left:10%;
    background-color: #124861b0;
    color:#61dafb;
    border: solid 1px #61dafb;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    z-index:99;
    padding:20px;
    a {
        text-decoration: none;
        cursor: pointer;
        font-size:15px;
    }
    h2 {
        text-transform: uppercase;
        font-weight: 100;
        margin-bottom:5px;
        padding:0;
        font-size:35px;
    }
}

@media (max-width:768px) {
    .Social-Wrapper {
        bottom:0px;
        left:calc(25% + 1px);
        h2 {
            text-transform: uppercase;
            font-weight: 100;
            padding:0;
            font-size:25px;
        }
    }
}